import "./styles/App.css";

import { HashRouter, Route, Link } from "react-router-dom";
import Home from "./home";
import Team from "./team";
import Events from "./event";
import About from "./about";
import Contact from "./contact";
import Addevent from "./addevent";
function App() {
  return (
    <HashRouter basename={process.env.PUBLIC_URL}>
      <div className="App">
        <div className="bg-warning p-3 ">
          <Link to="/">Codeflow</Link>
          <Link to="/">Home</Link>
          <Link to="/about">About</Link> <Link to="/events">Events</Link>
          <Link to="/team">Team</Link> <Link to="/contact">Contact Us</Link>
        </div>
        <Route path="/" exact={true} component={Home} />
        <Route path="/team" exact={true} component={Team} />
        <Route path="/events" exact={true} component={Events} />
        <Route path="/about" exact={true} component={About} />
        <Route path="/contact" exact={true} component={Contact} />
        <Route path="/Add" exact={true} component={Addevent} />
      </div>
    </HashRouter>
  );
}

export default App;
